input {
  border: none;
}

input:focus {
  border-color: yellow;
}

textarea {
  min-height: 40px;
}

/* Chrome, Safari, Edge, Opera */

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */

input[type=number] {
  -moz-appearance: textfield;
}

.modal {
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  display: block;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  background-color: rgb(0, 0, 0);
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.7);
  /* Black w/ opacity */
  -webkit-animation-name: animacao-abertura;
          animation-name: animacao-abertura;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}

form {
  position: relative;
  z-index: 2;
}
